.card-header.project-header-3{

    
    background-color: rgb(255, 255, 255);
    text-align: center;
    border-color: transparent;
  
  
  }
  
  .custom-project-card-1{
      padding: 32px 40px;
      background: rgb(255, 255, 255);
      border-radius: 3px;
      box-shadow: rgb(0 0 0 / 10%) 0px 0px 10px;
      box-sizing: border-box;
      color: rgb(94, 108, 132);
      border-color: transparent;
  
  }
  .section-name-createproject{
      font-size: 24px;
      font-style: inherit;
      line-height: 32px;
      font-weight: 500;
      color: rgb(23, 43, 77);
     
  
  }
  .custom-label-5{
      color: rgb(23, 43, 77);
      font-size: 14px;
      line-height: 1.42857;
      font-weight: 600;
  }
  .custom-forminput-6{
      font-size:14px !important;
      line-height: 1.42857;
      color: rgb(133,133,133);
      height: 38px;
      
  
  }
  .css-f8y4bk-control,.custom-forminput-6__control{
      padding: 0px !important;
      min-height: 35px !important;
      font-size:14px !important;
      line-height: 1.42857;
      color: rgb(133,133,133) !important;
  }
  .css-1uccc91-singleValue {
      color: rgb(133,133,133) !important;
  }
  
  .custom-forminput-6:active,.custom-forminput-6:focus,.css-f8y4bk-control:active,.css-f8y4bk-control:focus{
      border:2px solid #2684ff !important;
      box-shadow: none !important;
      font-size:14px !important;
      line-height: 1.42857;
  }
  .create-btn-prjct{
      -webkit-box-align: baseline;
      align-items: baseline;
      border-width: 0px;
      border-radius: 3px;
      box-sizing: border-box;
      font-style: normal;
      font-weight: 500;
      height: 40px;
      padding: 0px 10px;
      font-size: 14px;
      line-height:2.28571em;
  }
  .create-prjct{
      background-color: #0052CC;
      color: white;
  }
  .create-prjct:hover{
      background-color: #0065ff;
      color: white;
  }
  .cancel-prjct{
      line-height: 2.28571em;
      color: rgb(66, 82, 110) !important;
     
  }
  .cancel-prjct:hover{
      background-color: rgba(9, 30, 66, 0.04);
  }
  .error{
      font-size: 14px;
      font-family: inherit;
      font-weight: 400;
      margin-top: 5px;
      color: red;
  }
  .evms-from-create{
      padding-top: 0 !important;
     
      text-decoration: 1px solid #0052CC underline;
      color: #2684ff;
  }
  .evms-from-create:hover,.evms-from-create:focus{
      color: #0052cc;
      outline: transparent;
      border: 0 !important;
  }
  
  .ar-btn {
      margin-top: 30px;
  }
  
  .evms-div {
      border: 1px solid gray;
      border-radius: 5px;
  }
  
  .remove-file-ongoing{
      background: transparent;
      color: white;
      border-color: transparent;
  }
  
  .remove-file-ongoing:hover,.remove-file-ongoing .active,.remove-file-ongoing:focus{
      background: transparent !important;
     box-shadow: none !important;
      border-color: transparent !important;
      outline:none !important;
  }
  
  .remove-file-ongoing:focus {
      outline: 0 !important;
      border:transparent!important;
      box-shadow: none !important;
  }
  .input-info-msg {
      color: rgb(0, 155, 0);
      font-size: 12px;
  }
  .input-alert-msg {
      color: rgb(255, 40, 40);
      font-size: 12px;
  }

  .create-wbs-from-modal
{
    -webkit-box-align: baseline;
    align-items: baseline;
    border-width: 0px;
    border-radius: 3px;
    box-sizing: border-box;
    font-style: normal;
    font-weight: 500;
    height: 40px;
    width: 150px;
    padding: 0px 10px;
    font-size: 14px;
    line-height: 2.28571em;
    background-color: #0052cc;
    color: white;
}
.create-wbs-from-modal:hover{
    background-color: #0065ff;
    color: white;
}
  /**_____MEDIA QUERY______**/
  @media all and (min-width:300px) and (max-width:601px){
      .custom-project-card-1{
          padding:15px 0px;
      }
  }
  @media all and (min-width:992px){
      .project-form-button-holders{
          text-align: center !important;
      }
      .cancel-prjct{
          margin-left: 10px;
      }
  }